import React, { useCallback } from 'react';
import cn from 'classnames';
import { format } from 'date-fns';
import { YearsListYearProps } from 'shared/types/calendar';

import styles from './styles.scss';

const Year: React.FC<YearsListYearProps> = ({
  date,
  isToday,
  isActive,
  onYearClick
}) => {
  const handleClick = useCallback((): void => { onYearClick(date); }, [date, onYearClick]);

  return (
    <div
      className={ cn(styles.year, {
        [styles['year--today']]: isToday,
        [styles['year--active']]: isActive
      }) }
      onClick={ handleClick }
      role="presentation"
    >
      <span>{ format(date, 'yyyy') }</span>
    </div>
  );
};

export default Year;
